

import { InjectionToken } from '@angular/core';
import { WeekDayIndex } from 'tds-ui/core/time';

export interface TDSDateConfig {
  /** Customize the first day of a week */
  firstDayOfWeek?: WeekDayIndex;
}

export const TDS_DATE_CONFIG = new InjectionToken<TDSDateConfig>('date-config');

export const TDS_DATE_CONFIG_DEFAULT: TDSDateConfig = {
  firstDayOfWeek: undefined
};

export function mergeDateConfig(config: TDSDateConfig): TDSDateConfig {
  return { ...TDS_DATE_CONFIG_DEFAULT, ...config };
}
