

import { InjectionToken } from '@angular/core';

import { DateLocale, TDSI18nInterface } from './tds-i18n.interface';

export const TDS_I18N = new InjectionToken<TDSI18nInterface>('tds-i18n');

/** Locale for date operations, should import from date-fns, see example: https://github.com/date-fns/date-fns/blob/v1.30.1/src/locale/zh_cn/index.js */
export const TDS_DATE_LOCALE = new InjectionToken<DateLocale>('tds-date-locale');
