

import { Inject, Injectable, Optional } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { IndexableObject } from 'tds-ui/core/util';
import en_US from './languages/en_US';

import vi_VN from './languages/vi_VN';
import { DateLocale, TDSI18nInterface } from './tds-i18n.interface';
import { TDS_DATE_LOCALE, TDS_I18N } from './tds-i18n.token';

@Injectable({
  providedIn: 'root'
})
export class TDSI18nService {
  private _locale!: TDSI18nInterface;
  private _change = new BehaviorSubject<TDSI18nInterface>(this._locale);
  private dateLocale!: DateLocale;

  get localeChange(): Observable<TDSI18nInterface> {
    return this._change.asObservable();
  }

  constructor(@Optional() @Inject(TDS_I18N) locale: TDSI18nInterface, @Optional() @Inject(TDS_DATE_LOCALE) dateLocale: DateLocale) {
    this.setLocale(locale || vi_VN);
    this.setDateLocale(dateLocale || null);
  }

  // [NOTE] Performance issue: this method may called by every change detections
  // TODO: cache more deeply paths for performance
  translate(path: string, data?: any): string {
    // this._logger.debug(`[TDSI18nService] Translating(${this._locale.locale}): ${path}`);
    let content = this._getObjectPath(this._locale, path) as string;
    if (typeof content === 'string') {
      if (data) {
        Object.keys(data).forEach(key => (content = content.replace(new RegExp(`%${key}%`, 'g'), data[key])));
      }
      return content;
    }
    return path;
  }

  /**
   * Set/Change current locale globally throughout the WHOLE application
   * NOTE: If called at runtime, rendered interface may not change along with the locale change,
   * because this do not trigger another render schedule.
   *
   * @param locale The translating letters
   */
  setLocale(locale: TDSI18nInterface): void {
    if (this._locale && this._locale.locale === locale.locale) {
      return;
    }
    this._locale = locale;
    this._change.next(locale);
  }

  getLocale(): TDSI18nInterface {
    return this._locale;
  }

  getLocaleId(): string {
    return this._locale ? this._locale.locale : '';
  }

  setDateLocale(dateLocale: DateLocale): void {
    this.dateLocale = dateLocale;
  }

  getDateLocale(): DateLocale {
    return this.dateLocale;
  }

  /**
   * Get locale data
   * @param path dot paths for finding exist value from locale data, eg. "a.b.c"
   * @param defaultValue default value if the result is not "truthy"
   */
  getLocaleData(path: string, defaultValue?: any): any {
    const result = path ? this._getObjectPath(this._locale, path) : this._locale;

    if (!result && !defaultValue) {
      console.warn(`Missing translations for "${path}" in language "${this._locale.locale}".
You can use "TDSI18nService.setLocale" as a temporary fix.`);
    }

    return result || defaultValue || this._getObjectPath(en_US, path) || {};
  }

  private _getObjectPath(obj: IndexableObject, path: string): string | object | any {
    let res = obj;
    const paths = path.split('.');
    const depth = paths.length;
    let index = 0;
    while (res && index < depth) {
      res = res[paths[index++]];
    }
    return index === depth ? res : null;
  }
}
